








































export default {
	data() {
		const currentDate = this.getDate({
			format: true
		});

		return {
			index: 0,
			array: ['男', '女'],
			date: currentDate,
			user_data: '',
			util_list: [{
				name: '昵称',
				info: '编辑昵称',
				target: 'nickname',
			}, {
				name: '性别',
				info: '未设置',
				target: 'sex',
			}, {
				name: '出生日期',
				info: '未设置',
				target: 'birthday',
			}, {
				name: '身高',
				info: '未设置',
				target: 'height',
			}, {
				name: '手机号码',
				info: '未设置',
				target: 'phone',
			}, {
				name: '体重',
				info: '未设置',
				target: 'weight',
			}]
		};
	},
	computed: {
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		}
	},
	onShow: function() {
		let user_data = this.$store.state.userData;
		if (Array.isArray(user_data)) {
			this.setListData(user_data[0], user_data[1]);
		}
	},
	created: function() {

	},
	methods: {
		setListData(key, value) {
			this.util_list.forEach(function(item, index) {
				if (item.target == key) {
					item.info = value;
				}
			});
		},
		bindPickerChange(e) {
			console.log('picker发送选择改变，携带值为', e.target.value)
			this.index = e.target.value;
		},
		bindDateChange(e) {
			this.date = e.target.value;
		},
		openTag(item) {
			if (item.target != 'sex' && item.target != 'birthday') {
				this.open(item.target);
			}
		},
		open(type) {
			switch (type) {
				case 'nickname':
					this.target('datachecker', type, 'template');
					break;
				case 'sex':
					this.target('mpvuePicker', type, 'components');
					break;
				case 'height':
					this.target('datachecker', type, 'template');
					break;
				case 'weight':
					this.target('datachecker', type, 'template');
					break;
				case 'phone':
					this.target('datachecker', type, 'template');
					break;
				default:
					break;
			}
		},
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;

			return `${year}-${month}-${day}`;
		},
		target(tag, type, base) {
			uni.navigateTo({
				url: '../../' + base + '/' + tag + '/' + tag + '?type=' + type,
				success: res => {
					console.log(res)
				},
				fail: (res) => {
					console.log(res)
				}
			});
		}
	},
}



